.headline{
    font-size: 20px;
    background: rgba(0,0,0,.35);
    border: 1px solid black;
    height: 25px;
    margin-top: 3px;
    margin-bottom: 3px;
    overflow: -webkit-paged-x;
    overflow-x: hidden;
    -webkit-transition: ease-in-out .5s;
    -o-transition: ease-in-out .5s;
    transition: ease-in-out .5s; 
    -webkit-transition-delay: .15s; 
         -o-transition-delay: .15s; 
            transition-delay: .15s;
    /* transition: background ease-in-out .25s; */
}

.linkContainer{
    text-align: center;
    width: 85%;
    margin: auto;
}
.headline:hover{
background: rgba(0,0,0,.55);
height: 15vh;
overflow: auto;

}

.headline:hover .borderDiv{
    width: 100%;
}
.headline:hover .hideShow{
    opacity: 1;
}
.borderDiv{
    margin: auto;
    width: 0%;
    border-bottom: 3px solid red;
    -webkit-transition-delay: .25s;
         -o-transition-delay: .25s;
            transition-delay: .25s;
    -webkit-transition: .5s;
    -o-transition: .5s;
    transition: .5s;
}

.headlineLink{  
    margin: auto;
    display: inline-block;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    text-decoration: none;
    color: white;
    vertical-align: middle;
    -webkit-transition: .5 ease-in-out;
    -o-transition: .5 ease-in-out;
    transition: .5 ease-in-out;
}
.headlineLink:hover{
    color: #3498db;
}


.description{
    margin: 5px;
    font-size: 15px;
    color: white;
}

.sentimentBtn{
    font-weight: bold;
    color: black;
    margin-left: auto;
    margin-right: auto;
    margin-top: 5px;
    margin-bottom: 5px;
    display: block;
    padding: 3px;
    background: white;
}

.hideShow{
    opacity: 0;
    -webkit-transition: .5s;
    -o-transition: .5s;
    transition: .5s;
}
.sentiment{
    background: red;
}



